import { forwardRef, isValidElement } from "react";

/**
 * @description fix generic type with forwardRef
 * @reference https://www.totaltypescript.com/forwardref-with-generic-components
 */
export function fixedForwardRef<T, P = {}>(
  render: React.ForwardRefRenderFunction<T, P>
): (
  props: P & React.RefAttributes<T>
) => React.MemoExoticComponent<
  React.ForwardRefExoticComponent<P & React.RefAttributes<T>>
> {
  const result = forwardRef(render);
  if (isValidElement(result)) {
    throw new Error("Not valid element.");
  }
  return result as any;
}
